@import "./../../default/default.scss";

.page-404 {
  .block-404 {
    .bloc-404__container.container {
      padding-top: 75px;
      padding-bottom: 97px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      @media (max-width: $mobile) {
        padding-top: 30px;
        padding-bottom: 30px;
      }
      .block-404__title {
        font-family: "Montserrat";
        font-size: 248px;
        font-weight: 900;
        line-height: 248px;
        letter-spacing: 0.01em;
        text-transform: uppercase;
        background: url("/images/404.webp");
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-position: 50% 50%;
        @media (max-width: $mobile) {
            font-size: 140px;
        }
      }
      .block-404__description {
        margin-top: 4px;
        @include rale-500-18;
        word-spacing: 0.01em;
        letter-spacing: 0.01em;
      }
      .rectangle.unbackground {
        margin-top: 22px;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: baseline;
        gap: 12px;
        padding: 10px 23px 10px 24px;
        @include rale-600-16;
        line-height: 28px;
        letter-spacing: 0.02em;
        word-spacing: 0.01em;
        .icon-arrow {
          position: relative;
          &:before {
            display: inline-block;
            transform: scale(-1);
            font-size: 12px;
          }
        }
        @media (max-width: $mobile) {
            gap: 5px;
            padding-left: 10px;
            padding-right: 10px;
            font-size: 12px;
            .icon-arrow {
                &:before {
                    font-size: 10px;
                }
            }
        }
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
    }
  }
  .body-contacts{
    @media (max-width: $mobile) {
        padding-top: 30px;
        padding-bottom: 30px;
    }
  }
  .footer {
    padding-top: 52px;
    @media (max-width: $mobile) {
        padding-top: 30px;
    }
  }
}
