.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.page-404 .block-404 .bloc-404__container.container {
  padding-top: 75px;
  padding-bottom: 97px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (max-width: 600px) {
  .page-404 .block-404 .bloc-404__container.container {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
.page-404 .block-404 .bloc-404__container.container .block-404__title {
  font-family: "Montserrat";
  font-size: 248px;
  font-weight: 900;
  line-height: 248px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  background: url("/images/404.webp");
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-position: 50% 50%;
}
@media (max-width: 600px) {
  .page-404 .block-404 .bloc-404__container.container .block-404__title {
    font-size: 140px;
  }
}
.page-404 .block-404 .bloc-404__container.container .block-404__description {
  margin-top: 4px;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.01em;
  word-spacing: 0.01em;
  letter-spacing: 0.01em;
}
@media (max-width: 600px) {
  .page-404 .block-404 .bloc-404__container.container .block-404__description {
    font-size: 14px;
  }
}
.page-404 .block-404 .bloc-404__container.container .rectangle.unbackground {
  margin-top: 22px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: baseline;
  gap: 12px;
  padding: 10px 23px 10px 24px;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 0.01em;
  line-height: 28px;
  letter-spacing: 0.02em;
  word-spacing: 0.01em;
}
.page-404 .block-404 .bloc-404__container.container .rectangle.unbackground .icon-arrow {
  position: relative;
}
.page-404 .block-404 .bloc-404__container.container .rectangle.unbackground .icon-arrow:before {
  display: inline-block;
  transform: scale(-1);
  font-size: 12px;
}
@media (max-width: 600px) {
  .page-404 .block-404 .bloc-404__container.container .rectangle.unbackground {
    gap: 5px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 12px;
  }
  .page-404 .block-404 .bloc-404__container.container .rectangle.unbackground .icon-arrow:before {
    font-size: 10px;
  }
}
@media (max-width: 600px) {
  .page-404 .body-contacts {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
.page-404 .footer {
  padding-top: 52px;
}
@media (max-width: 600px) {
  .page-404 .footer {
    padding-top: 30px;
  }
}